import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { useForm } from "react-hook-form";
import { IUserRegisterData } from "../interface/user";
import { AuthService } from "../services/auth.service";
import { useNavigate, NavLink } from "react-router-dom";
import { UnauthorizedError, UserAlreadyExistsError } from "../../errors/unauthorized";
import { store } from "../../redux/store";
import { userCreatedAction } from "../../redux/user-slice";

export const Register: React.FC = () => {
  const { handleSubmit, register } = useForm<IUserRegisterData>();
  const [userNameExistError, setUserNameExistError] = useState<string>("");
  const navigate = useNavigate();
  const onSubmit = async (userData: IUserRegisterData) => {
    console.log(userData);
    try {
      const response = await AuthService.Register(userData);
      store.dispatch(
        userCreatedAction({
          username: userData.username,
          userID: response.userID,
        })
      );
      const loginDetails = {
        loginTime: new Date().toISOString(),
        maxAge: response.maxAge,
      };
      localStorage.setItem("loginAt", JSON.stringify(loginDetails));
      navigate("/");
    } catch (err) {
      if (err instanceof UserAlreadyExistsError) {
        setUserNameExistError(err.message);
      }
    }
  };

  return (
    <Form className="w-50 m-auto shadow p-3 mb-5 bg-white rounded " onSubmit={handleSubmit(onSubmit)}>
      <h2>Register</h2>
      <Form.Group className="mb-3" controlId="formBasicUsername">
        <Form.Label>Username</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter username"
          required
          {...register("username")}
          isInvalid={!!userNameExistError}
        />
        <Form.Control.Feedback type="invalid">{userNameExistError}</Form.Control.Feedback>
        <Form.Text className="text-muted">We'll never share your details with anyone else.</Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control type="password" required placeholder="Password" {...register("password")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicFirstName">
        <Form.Label>First Name</Form.Label>
        <Form.Control type="firstName" required placeholder="First Name" {...register("firstName")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicLastName">
        <Form.Label>Last Name</Form.Label>
        <Form.Control type="lastName" required placeholder="Last Name" {...register("lastName")} />
      </Form.Group>

      <Button variant="primary" type="submit">
        Submit
      </Button>
      <NavLink to={"/login"}>
        <Button variant="Secondary">Login</Button>
      </NavLink>
    </Form>
  );
};
