import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { useForm } from "react-hook-form";
import { IUserData } from "../interface/user";
import { AuthService } from "../services/auth.service";
import { useNavigate, NavLink, useLocation } from "react-router-dom";
import { UnauthorizedError } from "../../errors/unauthorized";
import { store } from "../../redux/store";
import { userCreatedAction } from "../../redux/user-slice";

export const Login: React.FC = () => {
  const { handleSubmit, register } = useForm<IUserData>();
  const [loginError, setLoginError] = useState<string>("");
  const navigate = useNavigate();
  const location = useLocation();
  const onSubmit = async (userData: IUserData) => {
    try {
      const response = await AuthService.login(userData);
      store.dispatch(
        userCreatedAction({
          username: userData.username,
          userID: response.userID,
        })
      );
      const loginDetails = {
        loginTime: new Date().toISOString(),
        maxAge: response.maxAge,
      };
      localStorage.setItem("loginAt", JSON.stringify(loginDetails));
      console.log((location.state as any).from);
      navigate((location.state as any).from);
    } catch (err) {
      if (err instanceof UnauthorizedError) {
        setLoginError(err.message);
      }
    }
  };

  return (
    <Form
      className="w-50 m-auto shadow p-3 mb-5 bg-white rounded "
      onSubmit={handleSubmit(onSubmit)}
    >
      <h2>Login</h2>
      <Form.Group className="mb-3" controlId="formBasicUsername">
        <Form.Label>Username</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter username"
          {...register("username")}
          isInvalid={!!loginError}
        />
        <Form.Control.Feedback type="invalid">
          {loginError}
        </Form.Control.Feedback>
        <Form.Text className="text-muted">
          We'll never share your details with anyone else.
        </Form.Text>
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label>Password</Form.Label>
        <Form.Control
          type="password"
          placeholder="Password"
          isInvalid={!!loginError}
          {...register("password")}
        />
        <Form.Control.Feedback type="invalid">
          {loginError}
        </Form.Control.Feedback>
      </Form.Group>

      <Button variant="primary" type="submit">
        Submit
      </Button>

      <NavLink to={"/register"}>
        <Button variant="Secondary">Register</Button>
      </NavLink>
    </Form>
  );
};
