import React from "react";
import { Container } from "react-bootstrap";
import { Navigate, Route, Routes, useLocation } from "react-router-dom";
import { About } from "../about/about.component";
import { Vacations } from "../vacations/vacations.component";
import { CreateVacation } from "../vacations/create-vacation/create-vacation.component";
import { Login } from "../login/login.component";
import { store } from "../../redux/store";
import { Home } from "../home/home.component";
import { Register } from "../register/register.component";
import { VacationChart } from "../vacations/vacation-chart.component";

export const Routing: React.FC = () => {
  const location = useLocation();
  return (
    <Container>
      <Routes>
        {!!store.getState().usersSlice.user.username && (
          <>
            <Route path="/" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/vacations" element={<Vacations />} />
            {store.getState().usersSlice.user.userID === 1 && (
              <Route path="/charts" element={<VacationChart />} />
            )}
            <Route path="/create-vacation" element={<CreateVacation />} />
            <Route path="/*" element={<Navigate to="/" />} />
          </>
        )}
        {store.getState().usersSlice.user.username === "" && (
          <>
            <Route path="/" element={<Home />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route
              path="/*"
              element={
                <Navigate to="/login" state={{ from: location.pathname }} />
              }
            />
          </>
        )}
        <Route path="/*" element={null} />
      </Routes>
    </Container>
  );
};

