import React, { useEffect, useMemo, useState } from "react";
import { IVacation } from "../interface/vacation";
import { VacationsService } from "../services/vacation.service";
import { VacationCard } from "./vacation-card/vacation-card.component";
import styles from "./vacations.styles.module.css";
import { Button, Pagination } from "react-bootstrap";
import { Link } from "react-router-dom";
import { store } from "../../redux/store";
import { EditVacationModal } from "./edit-vacation/edit-vacation.component";
import { DeleteVacationApprovalModal } from "./vacation-delete-modal";
import { vacationDownloadedAction } from "../../redux/vacations-slice";

const pageLimit: number = 10;

const paginateStyle = {
  width: "2.5em",
  "align-items": " center",
};

export const Vacations: React.FC = () => {
  const [vacations, setVacations] = useState<IVacation[]>([]);
  const [activePage, setActivePage] = useState<number>(1);
  const [filteredVacations, setFilteredVacations] = useState<IVacation[]>([]);
  const [pages, setPages] = useState<number[]>([]);
  const [isFollowingFiltered, setIsFollowingFiltered] = useState<boolean>(false);
  const [vacationData, setVacationData] = useState<IVacation>();
  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false);
  const [isRemoveModalOpen, setIsRemoveModalOpen] = useState<boolean>(false);

  const onPageClick = (page: number) => {
    setActivePage(page);
    setFilteredVacations(
      vacations
        .filter((v) => (isFollowingFiltered ? +v.isFollowing === 1 : vacations))
        .filter((v, i) => i >= page * pageLimit - pageLimit && i < page * pageLimit)
    );
  };

  const getNumberOfPages = (allVacations: IVacation[]) => {
    console.log("test");
    const items = [];
    for (
      let number = 1;
      number <=
      (allVacations.length % pageLimit === 0 ? allVacations.length / pageLimit : allVacations.length / pageLimit + 1);
      number++
    ) {
      items.push(number);
      setPages(items);
    }
  };

  async function getVacations() {
    try {
      const userID = store.getState().usersSlice.user.userID;

      if (userID) {
        const allVacations = await VacationsService.getVacations(userID);
        setVacations(allVacations);
        store.dispatch(vacationDownloadedAction(allVacations));
        getNumberOfPages(allVacations.filter((v) => (isFollowingFiltered ? +v.isFollowing === 1 : vacations)));

        const filterVacations = allVacations
          .filter((v) => (isFollowingFiltered ? +v.isFollowing === 1 : vacations))
          .filter((v, i) => i >= activePage * pageLimit - pageLimit && i < activePage * pageLimit);

        setFilteredVacations(filterVacations);
        if (!filterVacations.length) onPageClick(activePage - 1);
      }
    } catch (err) {
      console.log(err);
    }
  }

  useMemo(() => {
    const vacationList: IVacation[] = vacations.filter((v) => (isFollowingFiltered ? +v.isFollowing === 1 : vacations));
    setFilteredVacations(vacationList);
    getNumberOfPages(vacationList);
    setFilteredVacations(
      vacationList.filter((v, i) => i >= activePage * pageLimit - pageLimit && i < activePage * pageLimit)
    );
  }, [isFollowingFiltered]);

  const editClicked = (vacationData: IVacation) => {
    setVacationData(vacationData);
    setIsEditModalOpen(true);
  };

  const closeEditModal = () => {
    setIsEditModalOpen(false);
  };

  const removeClicked = (vacationData: IVacation) => {
    setVacationData(vacationData);
    setIsRemoveModalOpen(true);
  };

  const closeRemoveModal = () => {
    setIsRemoveModalOpen(false);
  };

  useEffect(() => {
    getVacations();
  }, []);

  return (
    <>
      {isRemoveModalOpen && (
        <DeleteVacationApprovalModal
          showModal={isRemoveModalOpen}
          vacationData={vacationData}
          handleClose={closeRemoveModal}
          reloadVacations={() => getVacations()}
        />
      )}

      {isEditModalOpen && (
        <EditVacationModal
          showModal={isEditModalOpen}
          vacationData={vacationData}
          handleClose={closeEditModal}
          reloadVacations={() => getVacations()}
        />
      )}

      {store.getState().usersSlice.user.userID === 1 ? (
        <Link to="/create-vacation">
          <Button className="mb-2" size="lg">
            Add a New Vacation
          </Button>
        </Link>
      ) : isFollowingFiltered ? (
        <Button className="mb-2" size="lg" onClick={() => setIsFollowingFiltered(false)}>
          show all
        </Button>
      ) : (
        <Button
          className="mb-2"
          size="lg"
          onClick={() => {
            setIsFollowingFiltered(true);
            setActivePage(1);
          }}
        >
          show followed vacations
        </Button>
      )}

      {pages.length > 1 && (
        <div>
          <Pagination /* size="lg" */>
            <Pagination.First onClick={() => onPageClick(1)} />
            <Pagination.Prev onClick={() => onPageClick(activePage === 1 ? 1 : activePage - 1)} />
            <Pagination.Item style={paginateStyle} onClick={() => onPageClick(pages[0])} active={1 === activePage}>
              1
            </Pagination.Item>
            {activePage >= 5 && <Pagination.Ellipsis />}
            {pages
              .filter((p) => {
                if (activePage <= 4) return p !== 1 && p <= 7 && p !== pages.length;
                if (pages.length - activePage === 3) return p !== 1 && p >= activePage - 3 && p !== pages.length;
                if (pages.length - activePage === 2) return p !== 1 && p >= activePage - 4 && p !== pages.length;
                if (pages.length - activePage === 1) return p !== 1 && p >= activePage - 5 && p !== pages.length;
                if (pages.length - activePage === 0) return p !== 1 && p >= activePage - 6 && p !== pages.length;

                return p !== 1 && p >= activePage - 2 && p <= activePage + 2 && p !== pages.length;
              })
              .map((p) => {
                return [
                  <Pagination.Item
                    style={paginateStyle}
                    onClick={() => onPageClick(p)}
                    key={p}
                    active={p === activePage}
                  >
                    {p}
                  </Pagination.Item>,
                ];
              })}
            {activePage + 3 < pages.length && <Pagination.Ellipsis />}
            <Pagination.Item
              style={paginateStyle}
              onClick={() => onPageClick(pages.length)}
              active={pages.length === activePage}
            >
              {pages.length}
            </Pagination.Item>
            <Pagination.Next onClick={() => onPageClick(activePage === pages.length ? pages.length : activePage + 1)} />
            <Pagination.Last onClick={() => onPageClick(pages.length)} />
          </Pagination>
        </div>
      )}

      <div className={styles.cardContainer}>
        {filteredVacations.map((v) => {
          return (
            <VacationCard
              key={v.id}
              vacation={v}
              reloadVacations={() => getVacations()}
              editVacation={editClicked}
              removeVacation={removeClicked}
            />
          );
        })}
      </div>
    </>
  );
};

