import { useState, useEffect } from 'react';
export function UseExpiredCookie() {
    const [timerId, setTimerId] = useState<NodeJS.Timer>();

    useEffect(() => {

        const timer = setInterval(() => {
            const loggedInAt = localStorage.getItem('loginAt')
            if (loggedInAt) {
                const loggedInAtParse = JSON.parse(loggedInAt)
                // console.log(loggedInAtParse)
                let loggedInTime = new Date(loggedInAtParse.loginTime);
                let now = new Date();
                // console.log('loggedInTime: ' + loggedInTime)
                // console.log('now: ' + now)

                // const msInMinute = 60 * 1000;
                const diff = Math.abs(now.getTime() - loggedInTime.getTime()) /* / msInMinute */
                // console.log("diff: " + diff + " maxAge: " + loggedInAtParse.maxAge)
                if (diff > loggedInAtParse.maxAge) {
                    window.location.reload()
                    localStorage.removeItem('loginAt')
                }
            }
        }, 1000)
        setTimerId(timer);
        return () => {
            if (timerId) {
                clearInterval(timerId);
            }
        };

    }, [])






}


