import React from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Controller, useForm } from "react-hook-form";
import Modal from "react-bootstrap/Modal";
import { IVacation, IVacationData } from "../../interface/vacation";
import { useEffect } from "react";
import { VacationsService } from "../../services/vacation.service";

interface editVacationProps {
  vacationData: IVacation | undefined;
  showModal: boolean;
  handleClose: () => void;
  reloadVacations: () => void;
}

export const EditVacationModal: React.FC<editVacationProps> = ({
  vacationData,
  showModal,
  handleClose,
  reloadVacations,
}) => {
  const {
    setError,
    handleSubmit,
    register,
    control,
    reset,
    formState: { errors },
    getValues,
  } = useForm<IVacationData>();

  //   useEffect(() => {
  //     return console.log("closed");
  //   }, []);

  async function onSubmit(data: IVacationData) {
    if (vacationData?.id) await VacationsService.updateVacation(data, vacationData?.id);
    reloadVacations();
  }

  return (
    <>
      <Modal show={showModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Form
          onSubmit={handleSubmit((data) => {
            onSubmit(data);
            handleClose();
          })}
        >
          <Modal.Body>
            <Form.Group className="mb-3" controlId="formDestination">
              <Form.Label>Destination</Form.Label>
              <Controller
                control={control}
                name="destination"
                defaultValue={vacationData?.destination}
                render={({ field: { onChange, onBlur, value, ref } }) => (
                  <Form.Control type="text" onChange={onChange} value={value} ref={ref} placeholder="Destination" />
                )}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicDescription">
              <Form.Label>Description</Form.Label>
              <Form.Control
                type="text"
                placeholder="Description"
                defaultValue={vacationData?.description}
                {...register("description")}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicFromDate">
              <Form.Label>From</Form.Label>
              <Form.Control
                type="date"
                placeholder="From"
                defaultValue={vacationData && new Date(vacationData?.fromDate).toLocaleDateString("fr-CA")}
                {...register("fromDate")}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicToDate">
              <Form.Label>To</Form.Label>
              <Form.Control
                type="date"
                placeholder="To"
                defaultValue={vacationData && new Date(vacationData?.toDate).toLocaleDateString("fr-CA")}
                {...register("toDate")}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPrice">
              <Form.Label>Price</Form.Label>
              <Form.Control
                type="number"
                placeholder="Price"
                defaultValue={vacationData?.price}
                {...register("price")}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicImage">
              <Form.Label>Image</Form.Label>
              <Form.Control accept="image/*" type="file" placeholder="image" {...register("image")} />
            </Form.Group>

            {/* <Button variant="primary" type="submit">
              Submit
            </Button> */}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" type="submit">
              Save Changes
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
};
