import { IVacation, IVacationData, IvacationUser } from '../interface/vacation';
import { handleResponse } from './handle-response';




const API_URL = 'http://localhost:3001'
const VACATIONS_Path_URL = '/api/v1/vacations'


export class VacationsService {


    static async getVacations(userID: number): Promise<IVacation[]> {
        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}/${userID}`, {
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',

        })
        return await handleResponse(response)
    }



    static getImgUrl(imagename: string): string {
        return `${API_URL}/images/${imagename}`
    }



    static async createVacation(data: IVacationData): Promise<IVacationData> {

        const formData = new FormData();
        formData.append('description', data.description);
        formData.append('destination', data.destination);
        formData.append('fromDate', data.fromDate.toString());
        formData.append('toDate', data.toDate.toString());
        formData.append('price', data.price.toString());
        formData.append('image', data.image[0]);



        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}`, {
            method: 'POST',
            credentials: "include",
            body: formData

        })
        return response.json()
    }

    static async updateVacation(data: IVacationData, vacationID: number): Promise<IVacationData> {

        const formData = new FormData();
        data.description && formData.append('description', data.description);
        data.destination && formData.append('destination', data.destination);
        data.fromDate.toString() && formData.append('fromDate', data.fromDate.toString());
        data.toDate.toString() && formData.append('toDate', data.toDate.toString());
        data.price.toString() && formData.append('price', data.price.toString());
        data.image[0] && formData.append('image', data.image[0]);



        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}/${vacationID}`, {
            method: 'PATCH',
            credentials: "include",
            body: formData

        })
        return response.json()
    }

    static async deleteVaction(id: number): Promise<void> {
        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}/${id}`, {
            method: "DELETE",
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',

        })
        return await handleResponse(response)
    }

    static async createUserVaction(userID: string, vacationID: string): Promise<void> {
        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}/user-vacation`, {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                userID,
                vacationID
            }),
            credentials: 'include',

        })
        return await handleResponse(response)
    }

    static async deleteUserVaction(userID: string, vacationID: string): Promise<void> {
        const response = await fetch(`${API_URL}${VACATIONS_Path_URL}/user-vacation/${userID}/${vacationID}`, {
            method: "DELETE",
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',

        })
        return await handleResponse(response)
    }





}

