import { IVacation } from '../components/interface/vacation';



export interface VacationsState {
  vacations: IVacation[]
}


const initialState: VacationsState = {
  vacations: []
};

// Vacations action types
export enum VacationsActionType {
  VacationsDownloaded = 'VacationsDownloaded',
  VacationsCreated = 'VacationsCreated',
  VacationsDelete = 'VacationsDelete'
}

// Vacations action:
export interface VacationAction {
  type: VacationsActionType;
  payload: any; //   מטען שילוח
}

// Action creators
export function vacationDownloadedAction(vacations: IVacation[]): VacationAction {
  return { type: VacationsActionType.VacationsDownloaded, payload: vacations };
}

export function vacationCreatedAction(vacation: IVacation): VacationAction {
  return { type: VacationsActionType.VacationsCreated, payload: vacation };
}
export function vacationDeleteAction(vacationID: number): VacationAction {
  return { type: VacationsActionType.VacationsDelete, payload: vacationID };
}



// vacation Reducer
export function vacationReducer(currentState: VacationsState = initialState, action: VacationAction) {
  const newState = { ...currentState }; //state is immutable

  switch (action.type) {
    case VacationsActionType.VacationsDownloaded:
      newState.vacations = action.payload;
      break;
    case VacationsActionType.VacationsCreated:
      newState.vacations.push(action.payload)
      break;
    case VacationsActionType.VacationsDelete:
      newState.vacations = newState.vacations.slice(newState.vacations.findIndex(v => v.id === action.payload), 1)
      break;
  }
  return newState;
}
