import React, { useEffect, useState, FC } from "react";
import { Unsubscribe } from "redux";
import {
  Button,
  Container,
  Nav,
  Navbar as NavbarBs,
  NavDropdown,
} from "react-bootstrap";
import { NavLink, useNavigate } from "react-router-dom";
import { store } from "../../../redux/store";
import { AuthService } from "../../services/auth.service";
import {
  userDownloadedAction,
  userLogoutAction,
} from "../../../redux/user-slice";

let unsubscribe: Unsubscribe;

export function Header() {
  const [user, setUser] = useState<string | undefined>("");
  const navigate = useNavigate();

  useEffect(() => {
    unsubscribe = store.subscribe(() => {
      setUser(store.getState().usersSlice.user.username);
    });
    return unsubscribe;
  }, [user]);

  const onLogout = async () => {
    await AuthService.logout();
    store.dispatch(userDownloadedAction({ username: "", userID: 0 }));
    navigate(`/login`, { state: "/" });
  };

  return (
    <NavbarBs sticky="top" className="bg-white shadow-sm mb-3 ">
      <Container>
        <Nav className="me-auto">
          <Nav.Link to="/" as={NavLink}>
            Home
          </Nav.Link>
          <Nav.Link to="/store" as={NavLink}>
            Store
          </Nav.Link>
          <Nav.Link to="/vacations" as={NavLink}>
            Vacations
          </Nav.Link>
          {store.getState().usersSlice.user.userID === 1 && (
            <Nav.Link to="/charts" as={NavLink}>
              Charts
            </Nav.Link>
          )}
          <Nav.Link to="/about" as={NavLink}>
            About
          </Nav.Link>
        </Nav>
        <div>
          {!user ? (
            <Nav.Link to="/login" as={NavLink}>
              Login
            </Nav.Link>
          ) : (
            <NavDropdown title={user} id="navbarScrollingDropdown">
              <NavDropdown.Item onClick={onLogout}>logout</NavDropdown.Item>
            </NavDropdown>
          )}
        </div>
      </Container>
    </NavbarBs>
  );
}
