import React from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Controller, useForm } from "react-hook-form";
import { IVacation, IVacationData } from "../../interface/vacation";
import { VacationsService } from "../../services/vacation.service";
import { useNavigate } from "react-router-dom";

export const CreateVacation: React.FC = () => {
  const {
    setError,
    handleSubmit,
    register,
    control,
    reset,
    formState: { errors },
    getValues,
  } = useForm<IVacationData>();

  const navigate = useNavigate();

  const onSubmit = async (data: IVacationData) => {
    try {
      await VacationsService.createVacation(data);
      navigate("/vacations");
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <Form.Group className="mb-3" controlId="formDestination">
        <Form.Label>Destination</Form.Label>
        <Controller
          control={control}
          name="destination"
          defaultValue=""
          render={({ field: { onChange, onBlur, value, ref } }) => (
            <Form.Control type="text" onChange={onChange} value={value} ref={ref} placeholder="Destination" />
          )}
        />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicDescription">
        <Form.Label>Description</Form.Label>
        <Form.Control type="text" placeholder="Description" {...register("description")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicFromDate">
        <Form.Label>From</Form.Label>
        <Form.Control type="date" placeholder="From" {...register("fromDate")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicToDate">
        <Form.Label>To</Form.Label>
        <Form.Control type="date" placeholder="To" {...register("toDate")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPrice">
        <Form.Label>Price</Form.Label>
        <Form.Control type="number" placeholder="Price" {...register("price")} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicImage">
        <Form.Label>Image</Form.Label>
        <Form.Control accept="image/*" type="file" placeholder="image" {...register("image")} />
      </Form.Group>

      <Button variant="primary" type="submit">
        Submit
      </Button>
    </Form>
  );
};
