import { IUserStore } from '../components/interface/user';

export interface UserState {
  user: IUserStore
}


//TODO ask gut if is ok to use redux for keeping user login
const initialState: UserState = {
  user: {
    username: undefined,
    userID: undefined


  },
};

// Products action types
export enum UsersActionType {
  UserDownloaded = 'UserDownloaded',
  UserCreated = 'UserCreated',
  UserLogOut = 'UserLogOut'
}

// Products action:
export interface UserAction {
  type: UsersActionType;
  payload: any; //   מטען שילוח
}

// Action creators
export function userDownloadedAction(user: IUserStore): UserAction {
  return { type: UsersActionType.UserDownloaded, payload: user };
}

export function userCreatedAction(user: IUserStore): UserAction {
  return { type: UsersActionType.UserDownloaded, payload: user };
}
export function userLogoutAction(): UserAction {
  return { type: UsersActionType.UserDownloaded, payload: initialState };
}



// user Reducer
export function userReducer(currentState: UserState = initialState, action: UserAction) {
  const newState = { ...currentState }; //state is immutable

  switch (action.type) {
    case UsersActionType.UserDownloaded:
      newState.user = action.payload;
      break;
    case UsersActionType.UserCreated:
      newState.user = action.payload;
      break;
    case UsersActionType.UserLogOut:
      newState.user = action.payload;
      break;
  }
  return newState;
}
