import { IUserData } from '../interface/user';
import { handleResponse } from './handle-response';





const API_URL = 'http://localhost:3001/api/v1/auth'


export class AuthService {


    static async login(userData: IUserData): Promise<{ maxAge: string, userID: number }> {


        const response = await fetch(`${API_URL}/login`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: "include",
            body: JSON.stringify(userData)

        })
        return await handleResponse(response)

    }

    static async Register(userData: IUserData): Promise<{ maxAge: string, userID: number }> {
        console.log(userData)

        const response = await fetch(`${API_URL}/register`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: "include",
            body: JSON.stringify(userData)

        })
        return await handleResponse(response)

    }


    static async me(): Promise<{ username: string, userID: number }> {
        const response = await fetch(`${API_URL}/me`, {

            credentials: 'include',
        });
        return await handleResponse(response);
    }

    static async logout(): Promise<void> {
        await fetch(`${API_URL}/logout`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',
        });
    }

}

