import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Layout } from "./components/layout/layout.component";
import { UnauthorizedError } from "./errors/unauthorized";
import { AuthService } from "./components/services/auth.service";
import { store } from "./redux/store";
import { userCreatedAction, userDownloadedAction } from "./redux/user-slice";
import { UseExpiredCookie } from "./hooks/useExpiredCookie";

const path = window.location.pathname;

export const App: React.FC = () => {
  const [user, setUser] = useState<string>();
  const navigate = useNavigate();
  UseExpiredCookie();
  const checkLogin = async () => {
    try {
      const { username, userID } = await AuthService.me();
      setUser(username);
      store.dispatch(userDownloadedAction({ username, userID }));
    } catch (err) {
      if (err instanceof UnauthorizedError) {
        setUser("");
        store.dispatch(userDownloadedAction({ username: "", userID: 0 }));
        if (window.location.pathname !== "/register") {
          navigate(`/login`, { state: { from: path } });
        }
      }
    }
  };

  useEffect(() => {
    checkLogin();
  }, [user]);

  return (
    <div className="App">
      <Layout />
    </div>
  );
};

export default App;
