import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  LineElement,
} from "chart.js";
import { Bar } from "react-chartjs-2";
import React, { useState } from "react";
import { store } from "../../redux/store";
import { useEffect } from "react";
import { IVacation, IVacationChartData } from "../interface/vacation";
import { VacationsService } from "../services/vacation.service";
import { useMemo } from "react";

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, BarElement, Title, Tooltip, Legend);

export const VacationChart: React.FC = () => {
  const [vacations, setVacations] = useState<IVacation[]>([]);
  const [vacationData, setVacationData] = useState<IVacationChartData>();

  useMemo(() => {
    setVacationData({
      labels: vacations.filter((v) => v.followers > 0).map((v) => v.destination),
      datasets: [
        {
          label: "Followers",
          data: vacations.filter((v) => v.followers > 0).map((v) => v.followers),
          backgroundColor: "#0d6efd",
        },
      ],
    });
  }, [vacations]);

  const getVacations = async () => {
    const vacationsFromStore = store.getState().vacationsSlice.vacations;

    if (vacationsFromStore.length > 0) {
      setVacations(vacationsFromStore);
    } else {
      try {
        const userID = store.getState().usersSlice.user.userID;
        if (userID) {
          const vacationsFromServer = await VacationsService.getVacations(userID);
          setVacations(vacationsFromServer);
        }
      } catch (err) {
        console.log(err);
      }
    }
  };

  useEffect(() => {
    getVacations();
  }, []);

  return <div>{vacationData && <Bar data={vacationData} />}</div>;
};
