import React from "react";
import { IVacation } from "../../interface/vacation";
import styles from "./../vacations.styles.module.css";
import { VacationsService } from "../../services/vacation.service";
import { Button } from "react-bootstrap";
import { store } from "../../../redux/store";
//TODO ask guy id can I use module style in 2 tsx files

interface VacationCardProps {
  vacation: IVacation;
  reloadVacations: () => void;
  editVacation: (vacationData: IVacation) => void;
  removeVacation: (vacationData: IVacation) => void;
}

export const VacationCard: React.FC<VacationCardProps> = ({
  vacation,
  reloadVacations,
  editVacation,
  removeVacation,
}) => {
  const followVacation = async (userId: string, vacationId: string) => {
    await VacationsService.createUserVaction(userId, vacationId);
    reloadVacations();
  };
  const unFollowVacation = async (userId: string, vacationId: string) => {
    await VacationsService.deleteUserVaction(userId, vacationId);
    reloadVacations();
  };

  const imgUrl = VacationsService.getImgUrl(vacation.imageName);
  const UserID = store.getState().usersSlice.user.userID;

  let deleteProps = {
    style: UserID === 1 ? { cursor: "pointer" } : { cursor: "not-allowed" },
    onClick: UserID === 1 ? () => removeVacation(vacation) : undefined,
    className: styles.stat,
  };
  let editProps = {
    style: UserID === 1 ? { cursor: "pointer" } : { cursor: "not-allowed" },
    onClick: UserID === 1 ? () => editVacation(vacation) : undefined,
    className: styles.stat,
  };

  return (
    <div className={styles.card}>
      <div style={{ background: `url("${imgUrl}")`, textAlign: "right" }} className={styles.cardImage}>
        {UserID !== 1 ? (
          !vacation.isFollowing ? (
            <Button onClick={UserID ? () => followVacation(UserID.toString(), vacation.id.toString()) : undefined}>
              Follow!
            </Button>
          ) : (
            <Button onClick={UserID ? () => unFollowVacation(UserID.toString(), vacation.id.toString()) : undefined}>
              Unfollow!
            </Button>
          )
        ) : null}
      </div>

      <div className={styles.cardText}>
        <h2>{vacation.destination}</h2>
        <p>description: {vacation.description}</p>
        <p>price: {vacation.price}</p>
        <p>From: {new Date(vacation.fromDate).toLocaleDateString()}</p>
        <p>To: {new Date(vacation.toDate).toLocaleDateString()}</p>
      </div>
      <div className={styles.cardStats}>
        <div {...deleteProps}>
          <div className={styles.type}>Delete</div>
        </div>
        <div className={`${styles.stat} ${styles.border}`}>
          <div className={styles.value}>{vacation.followers}</div>
          <div className={styles.type}>followers</div>
        </div>
        <div {...editProps}>
          <div className={styles.type}>Edit</div>
        </div>
      </div>
    </div>
  );
};
