import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useEffect } from "react";
import { Unsubscribe } from "redux";
import { store } from "../../redux/store";
import { IVacation } from "../interface/vacation";
import { VacationsService } from "../services/vacation.service";

export interface DeleteVacationApprovalProps {
  vacationData: IVacation | undefined;
  showModal: boolean;
  handleClose: () => void;
  reloadVacations: () => void;
}

export const DeleteVacationApprovalModal: React.FC<DeleteVacationApprovalProps> = ({
  vacationData,
  showModal,
  handleClose,
  reloadVacations,
}) => {
  const removeVacationFromServer = async () => {
    vacationData && (await VacationsService.deleteVaction(vacationData?.id));

    handleClose();
    reloadVacations();
  };

  return (
    <>
      <Modal show={showModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>are you sure you desire remove {vacationData?.destination}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p> you're going to remove that vacation from the server! </p>
          {vacationData?.followers ? (
            vacationData?.followers === 1 ? (
              <p> there is 1 follower to this vacation!</p>
            ) : vacationData?.followers > 1 ? (
              <p>there are {vacationData?.followers} followers to this vacation!</p>
            ) : null
          ) : null}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button
            variant="danger"
            onClick={() => {
              handleClose();
              removeVacationFromServer();
            }}
          >
            Delete
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
